<script>
  export let data
  export let characters

const searchCharacters = event => {
  characters = data.filter(character => {
    let search = event.target.value.toLowerCase()
    let name = character.name.toLowerCase()
    return name.includes(search)
  })
}
</script>


<style>

  input {
    margin: auto;
    display: block;
    text-align: center;
    margin-bottom: 2%;
    font-size: 24px;
    color: black;
    background-color: rgb(189, 195, 199);
  }
  </style>

<div class="search">
  <input
  placeholder="search characters..."
  on:input={searchCharacters}
/>
</div>
