<script>
  import Title from "./Title.svelte"
  import Cards from "./Cards.svelte"
  import Loading from "./Loading.svelte"
  import { onMount } from "svelte"
  import SearchBox from "./SearchBox.svelte"

  //const url = "https://gateway.marvel.com:443/v1/public/characters?limit=100&offset=0&ts=thesoer&apikey=ccfdb54fc361f803d07855f3895d14b0&hash=8985759a11dab023d6f13a17d1fba148"
const  url = "/characters.json"
  let data, characters = null

  onMount(async () => {
    const results = await fetch(url)
    data = await results.json()
    //console.log(data)
    characters = data
  })
</script>

<style>
.content {
  width: 70%;
   margin: auto;
    background-color: yellow;
  }
.cards {
  background-color: rgb(189, 195, 199);
   border-radius: 20px;
   display: flex;
   flex-wrap: wrap;
   justify-content: space-evenly;
 }
</style>

<div class="content">
  <Title />
  <SearchBox data={data} bind:characters={characters} />
  {#if data == null}
    <Loading />
  {:else}
  <div class="cards">
    <Cards characters={characters}/>
  </div>
  {/if}
</div>
